<script>
export default {
  data() {
    return {
      // 圓餅圖設定
      ChartOption1: {
        tooltip: {
          trigger: "item",
          formatter: "{a} <br/>{b}: {c} ({d}%)",
        },
        legend: {
          orient: "vertical",
          left: 10,
          data: ["工業用水", "生活用水"],
        },
        series: [
          {
            type: "pie",
            radius: ["50%", "70%"],
            avoidLabelOverlap: false,
            label: {
              show: false,
              position: "center",
            },
            emphasis: {
              label: {
                show: true,
                fontSize: "30",
                fontWeight: "bold",
              },
            },
            labelLine: {
              show: false,
            },
            data: [
              { value: 40, name: "工業用水" },
              { value: 60, name: "生活用水" },
            ],
          },
        ],
      },
      mathContent: "",
    };
  },
  methods: {
    // 實際水費例子中點選特定項目看算法
    mathContext(name) {
      var mathContent = document.getElementById("math");
      if (name == "use") {
        mathContent.innerHTML = "";
        mathContent.innerHTML +=
          "用水費 : <br/>25 ( 用水度數 ) * 9.45 ( 對應表一第二段的每度單價 ) + (-21) ( 累進差額 ) <br/>= 215.25元";
      }
      if (name == "basic") {
        mathContent.innerHTML = "";
        mathContent.innerHTML +=
          "基本費 : <br/>35.7元 ( 對應到表二的20mm基本費 )";
      }
      if (name == "conserve") {
        mathContent.innerHTML = "";
        mathContent.innerHTML +=
          "水源保育與回饋費 : <br/>215.25 ( 用水費 ) * 0.05 = 10.7625 -> 約11元";
      }
      if (name == "clean") {
        mathContent.innerHTML = "";
        mathContent.innerHTML +=
          "清除處理費 : <br/>25 ( 用水度數 ) * 4.1元 ( 對應到表三的高雄 ) = 102.5 -> 約103元";
      }
      if (name == "sum") {
        mathContent.innerHTML = "";
        mathContent.innerHTML +=
          "應繳水費總金額為 : <br/>215.25 + 35.7 + 11 + 103 = 364.95 -> 約365元";
      }
    },
  },
};
</script>
